import kTeam from "@/components/k-ui/k-team/k-team.vue"
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"
import kManualTeam from "@/components/k-ui/k-manual/k-manual-team.vue"
import {
	mapState
} from 'vuex';
export default {
	components:{
		kTeam,
		uniNumberBox,
		kManualTeam
	},
	data() {
		return {
			user:'',
			datas: [],
			modalName: '',
			teamName: '',
			contact: '',
			teamBrief: '',
			currGameId: '',
			need: 1,
			teamSize: '',
			createBtnShow: true
		};
	},
	mounted() {
	    this.user = this.$store.getters.getUserInfo
	},
	computed: {
		...mapState(['hasLogin', 'userInfo']),
	},
	onLoad(options) {
		console.log(options)
		uni.showLoading({
			title: '正在加载'
		})
		this.currGameId = options.id
		this.teamSize = options.teamSize
		this.loadData()
	},
	methods:{
		test() {
			console.log(this.user)
			this.isBoundWx()
		},
		manualClick(e){
			this.createBtnShow = e?false:true
			this.hideModal()
		},
		needChange(e) {
			this.need = e
			if(e==this.teamSize){
				this.$api.msg("已达人数上限~")
			}
		},
		goTeamDetail(id){
			// console.log(id)
			uni.navigateTo({
				url: '/pages/team/my-team-detail?id=' + id
			})
		},
		complete(msg){
			uni.showModal({
				title: '提示',
				content: msg,
				showCancel: true,
				confirmText: '去设置',
				success: (e) => {
					if (e.confirm) {
						uni.navigateTo({
							url: '/pages/set/personInfo/personInfo'
						})
					}
				}
			})
		},
		showModal(e) {
			if(this.user.contact==""){
				this.complete("请完善您的联系方式~")
				return
			}
			this.modalName = e.currentTarget.dataset.target
			console.log(e.currentTarget.dataset.target)
		},
		hideModal(e) {
			this.modalName = ''
			this.teamName = ""
			this.teamBrief = ""
			this.contact = ""
		},
		createModal(){			
			if(this.teamName == "" || this.teamBrief == "" || this.contact == ""){
				this.$api.msg("请完善信息~")
				return
			}
			this.create()
			this.hideModal()
		},
		async isBoundWx(){
			const res = await this.$api.request({
				url: '/isBoundWx/' + this.user.id,
			})
			console.log(res)
			if(res.data.isBoundWeChat == 0) {
				uni.showModal({
					title: '提示',
					content: '检测到您未关注‘i瓜大’公众号，无法为您推送组队信息',
					showCancel: false,
					confirmText: '知道了',
					confirmColor:'#8c8b83',
					success(res2) {
					}
				});
			}
		},
		async create(){
			const res = await this.$api.request({
				url: '/team/create',
				method: "POST",
				contentType: "application/json",
				data: {
					userId:this.user.id,
					gameId: this.currGameId,
					name: this.teamName,
					contact: this.contact,
					size: this.need,
					brief: this.teamBrief
				}
			})
			if(res.status == 200) {
				this.loadData()
				uni.showToast({
					title: '创建成功~'
				})
				setTimeout(()=>{
					this.isBoundWx()
				}, 1500)
				
			}else if(res.status.startsWith("1000")){
				this.$api.msg(res.msg)
			} else {
				this.$api.msg("服务器正忙~")
			}
			console.log(res)
		},
		async loadData() {
			const res = await this.$api.request({
				url: '/team/' + this.currGameId,
			})
			this.datas = res.data
			console.log(res)
			uni.hideLoading()
		},
		goUserDetail(id) {
			uni.navigateTo({
				url: '/pages/person/person-detail?id=' + id
			})
		},
		async unApply(id){
			const res = await this.$api.request({
				url: '/team/unApply/' + id,
			})
			if(res.status == 200) {
				this.loadData()
				uni.showToast({
					title:'取消成功~'
				})
			}else {
				this.$api.msg(res.msg)
			}
		},
		async apply(id){
			const res = await this.$api.request({
				url: '/team/apply/' + id,
			})
			if(res.status == 200) {
				this.loadData()
				uni.showToast({
					title:'申请成功~'
				})
			}else {
				this.$api.msg(res.msg)
			}
		},
		quit(id){
			uni.showModal({
				title: '退出提示',
				content: '您确定退出队伍吗？',
				showCancel: true,
				confirmText: '确定',
				success: (e) => {
					if (e.confirm) {
						this.unApply(id)
					}
				}
			})
		},
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
		btnClick() {
			// console.log('btnClick');
			this.$refs.kManualTeam.showModal();
		},
		btnTouchstart() {
			// console.log('btnTouchstart');
		},
		btnTouchend() {
			// console.log('btnTouchend');
		},
	},
	//下拉刷新
	onPullDownRefresh() {
		uni.showLoading({
			title: '正在加载'
		})
		this.datas = []
		this.loadData()
		setTimeout(()=>{
			uni.stopPullDownRefresh();
			uni.showToast({
				title:'刷新成功'
			})
		}, 1000)
		
	}
}
